  border: none;
  border-radius: 50%;
  color: white;
  width: 22px;
  height: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.2s ease;
  font-size: 14px;
  padding: 0;
}
.clear-pdf-btn:hover {
  background: rgba(123, 123, 123, 0.7);
}
.input-quiz-option-main {
  border-radius: 8px;
  border: 2px solid var(--background);
  width: 40%;
  text-align: center;
  height: 1.5rem;
  font-size: 15;
  background-color: white;
  color: black;
}
.input-quiz-option-secondary {
  border-radius: 8px;
  border: 2px solid var(--background);
  width: 35%;
  text-align: center;
  background-color: white;
  color: black;
}
.input-quiz-option-secondary-two {
  border-radius: 8px;
  border: 2px solid var(--background);
  width: 25%;
  text-align: center;
  margin: 0px 10px;
  height: 1.5rem;
  background-color: white;
  color: black;
}
.main-text {
  margin-left: 15rem;
  font-size: 25px;
}
.youtube-Bar {
  border-radius: 8px;
}
.layout {
  display: flex;
  min-height: calc(100vh - 60px);
}
.main-content {
  flex: 1;
  padding: 20px;
}
.sidebar {
  margin: 0;
  padding: 5px 1em;
  font-family: Poppins;
  line-height: 1.5rem;
  background-color: var(--base-clr);
  color: var(--text-clr);
  flex-direction: column;
  height: 100vh;
  width: 230px;
  border-right: 1px solid var(--text-clr);
  box-sizing: border-box;
  display: flex;
  position: sticky;
  top: 0;
  align-self: start;
  transition: 300ms ease-in-out;
}
.sidebarClose {
  padding: 7px;
  width: 60px;
}
.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.sidebar ul li {
  margin-bottom: 16px;
}
.sidebar > ul > li:first-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  padding: 8px;
  width: 100%;
  font-weight: bold;
  font-size: 18px;
}
.SideBySide {
  display: flex;
  align-items: center;
  gap: 15px;
  width: 100%;
  padding: 10px 10px;
  border-radius: 6px;
}
.SideBySide:hover {
  background-color: var(--hover-clr);
}
.icon-wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 2px;
  border-radius: 10px;
}
.icon-wrapper:hover svg {
  color: var(--accent-clr);
  background-color: var(--hover-clr);
}
.history-quiz-list {
  margin-top: 8px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.history-quiz-padding-bottom {
  padding-bottom: 8px;
}
.history-quiz-item {
  position: relative;
  height: 15rem;
  width: 17rem;
  background: #ffffff;
  border: 2px solid #5e63ff;
  border-radius: 6px;
  font-size: 0.7rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.quiz-top {
  flex: 1;
  background: #e0dede;
}
.quiz-top-icon {
  margin-left: 7rem;
  margin-top: 2rem;
}
.quiz-bottom {
  flex: 1;
  background-color: #000;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem;
  text-align: center;
  font-family: Poppins;
}
.quiz-buttons {
  display: flex;
  gap: 0.5rem;
}
.youtube-thumb {
  position: relative;
  padding: 0;
}
.youtube-thumbnail {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 6px;
}
.quizPageTitleText {
  font-size: 30px;
  color: var(--accent-clr);
  font-family: Poppins;
  font-weight: bold;
}
.quizPageQuestionText {
  font-size: 18px;
  font-family: Poppins;
  color: white;
}
.quizPageOptions {
  background-color: black;
  color: white;
  border-radius: 6px;
  padding: 10px;
  border: 2px solid var(--text-clr);
  font-family: Poppins;
}
.submitQuizButton {
  border: 2px solid var(--text-clr);
  border-radius: 6px;
  padding: 10px;
  background-color: var(--accent-clr);
  color: white;
  font-family: Poppins;
}
.score {
  font-family: Poppins;
  color: white;
  font-weight: bold;
  font-size: 25px;
}
.select-all {
  border: 2px solid var(--text-clr);
  padding: 5px;
  border-radius: 6px;
  color: white;
}
.google-drive-picker-wrapper {
  margin-bottom: 10px;
  position: relative;
  z-index: 5;
}
.google-drive-connect {
  margin-top: 10px;
}
.google-drive-connect-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background-color: white;
  border: 2px solid #ddd;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  color: black;
  transition: background-color 0.2s ease;
}
.google-drive-connect-btn:hover {
  background-color: #f5f5f5;
}
.google-drive-connect-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.google-drive-open-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background-color: white;
  border: 2px solid #ddd;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  color: black;
  transition: background-color 0.2s ease;
}
.google-drive-open-btn:hover {
  background-color: #f5f5f5;
}
.google-drive-open-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.google-drive-picker {
  position: relative;
  background-color: white;
  border: 2px solid #ddd;
  border-radius: 8px;
  padding: 16px;
