  flex-direction: column;
  z-index: 20;
  margin-bottom: 10px;
}
.google-drive-picker-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  font-weight: 600;
  color: black;
}
.close-picker-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  transition: color 0.2s ease;
}
.close-picker-btn:hover {
  color: black;
}
.google-drive-search {
  display: flex;
  gap: 8px;
  margin-bottom: 12px;
}
.google-drive-search input {
  flex: 1;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 0.9rem;
  color: black;
  outline: none;
}
.google-drive-search input:focus {
  border-color: var(--accent-clr);
}
.google-drive-search button {
  padding: 8px 16px;
  background-color: var(--accent-clr);
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: opacity 0.2s ease;
}
.google-drive-search button:hover {
  opacity: 0.9;
}
.google-drive-search button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.google-drive-files-list {
  flex: 1;
  overflow-y: auto;
  max-height: 300px;
}
.google-drive-file-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px;
  border: 1px solid #eee;
  border-radius: 6px;
  margin-bottom: 8px;
