  padding: 20px;
  color: #666;
  font-size: 0.9rem;
}
.selected-drive-files {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 8px;
}
.selected-drive-file-item {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 12px;
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 0.8rem;
  color: black;
}
.remove-drive-file-btn {
  background: none;
  border: none;
